import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLogin: 0,
    username: "",
    backend: "http://192.168.1.100:3333",
    searchType:"a_name",
    category: "%",
    year:"<=2019",
    month:"%",
    orderWay:"playTimes desc",
  },
  mutations: {
    changeLogin(state,status,username){
      state.isLogin = status;
      state.username = username;
    },
    changeSearchType(state,status){
      state.searchType = status;
    },
    changeCategory(state,status){
      state.category = status;
    },
    changeYear(state,status){
      state.year = status;
    },
    changeMonth(state,status){
      state.month = status;
    },
    changeOrderWay(state,status){
      state.orderWay = status;
    }
  },
  actions: {
    loginAction({commit}){
      localStorage.setItem('isLogin', 1);
      commit('changeLogin', 1, localStorage.getItem('username'));
      //alert(localStorage.getItem('isLogin'))
    },
    logoutAction({commit}){
      localStorage.setItem('isLogin', 0);
      commit('changeLogin', 0, localStorage.getItem('username'));
      //alert(localStorage.getItem('isLogin'))
    },
    searchTypeAction({commit}, data){
      commit('changeSearchType', data)
    },
    categoryAction({commit}, data){
      commit('changeCategory', data)
    },
    yearAction({commit}, data){
      commit('changeYear', data)
    },
    monthAction({commit}, data){
      commit('changeMonth', data)
    },
    orderWayAction({commit}, data){
      commit('changeOrderWay', data)
    }
  }
})
