<template>
  <div id="app">
    <div id="nav">
      <span id="logout" @click="logout()"><u>Logout</u></span> |
      <router-link to="/login" @click="reload()" :style="note">{{login}}</router-link> |
      <router-link id="rtlink" to="/" @click="reload()">Day Mode</router-link> |
      <router-link id="rtlink" to="/about" @click="reload()">Night Mode</router-link> |
      <span id="changeBackend" @click="backend()"><u>Backend</u></span>
    </div>
    <router-view/>
  </div>
</template>

<style>
#app {
  font-family: "hansan", "helveti";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin:0;
  padding:0;
  border: 0;
}
#nav {
  padding: 0px;
  float:right;
  position:fixed;
  right:30px;
  top:30px;
  z-index: 100;
}
#logout{
  font-weight: bold;
  color:darkgrey;
}
#changeBackend{
  font-weight: bold;
  color:darkgrey;
}
#rtlink {
  font-weight: bold;
  color:darkgrey;
}

#nav a.router-link-exact-active {
  color:gray;
}
</style>

<script>
import {mapActions, mapState} from 'vuex'

export default {
  name: 'app',
  mounted(){
    this.showLogin = true;
  },
  computed: {
    //...mapState(['isLogin'])
    isLogin: {
        get:function(){ 
          return this.$store.state.isLogin
        },
        set:function(newValue){
          this.$store.state.isLogin = newValue;
        }
    }
  },
  data(){
    return{
      login: "Login",
      note:{
        color: "darkgrey",
        "font-weight": "bold",
      }
    }
  },
  provide(){
    return{
      reload: this.reload
      }
  },
  methods: {
    ...mapActions(['isLogin']),
    backend:function(){
      this.$prompt("后端网址：",{
                    confirmButtonText: '确认',
                }).then(({value}) => {
                    this.$store.state.backend = value
                    localStorage.setItem('backend', value)
                })
    },
    logout:function(){
      if(this.$store.state.isLogin == 0){
        this.$message({
          type:'info',
          message:'已处于未登录状态！'
        })
      }else if(confirm('即将退出登录')){
        localStorage.setItem('username', "")
        localStorage.setItem('isLogin', 0)
        this.$store.dispatch('logoutAction')
        this.$message({
          type:'info',
          message:'退出成功！'
        })
        this.reload()
      }
      if(this.$route.path == '/profile'){
        this.$router.push('/')
      }
    },
    reload:function(){
      if(localStorage.getItem('isLogin') == 1){
        this.login = localStorage.getItem('username')
        this.note.color = "#fb7299"
      }
      else{
        this.login = "Login"
        this.note.color = "darkgrey"
      }
    }
  },
  mounted: function() {

  },
  created(){
    if(localStorage.getItem('isLogin') == 1){
      this.login = localStorage.getItem('username')
      this.note.color = "#fb7299"
      this.$store.dispatch('loginAction')
    }
    else{
      this.login = "Login"
      this.note.color = "darkgrey"
    }
    if(localStorage.getItem('backend') == null || localStorage.getItem('backend') == ""){
      this.backend()
    }
    else{
      this.$store.state.backend = localStorage.getItem('backend')
    }
  }

}
</script>
